#Credential{
    width: 100%;
    height: 100%;
    padding-top: 0.5rem;
    -webkit-animation: FadeIn 2s;
            animation: FadeIn 2s;
}
#Credential>div:first-child{
    width: 100%;
}
#Credential>div.slick-slider{
    height: 85%;
}
#Credential>div.slick-slider>div.slick-list,
#Credential>div.slick-slider>div.slick-list>div.slick-track,
#Credential>div.slick-slider>div.slick-list>div.slick-track>div>div{
    height: 100%;
}
#Credential div.FormSnippet{
    height: 100%;
    padding: 0.25rem 0.5rem;
    position: relative;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    -webkit-transition: 0.75s;
    transition: 0.75s;
}
#Credential div.FormSnippet:focus{
    outline: none !important;
}
#Credential div.FormSnippet>div{
    border-radius: 0.5rem;
    box-shadow: 0px 0px 1.5rem 0 var(--color-black-light);
    height: calc(100% - 1rem);
    color: var(--color-white-normal);
}
#Credential .slick-slide.slick-center div.FormSnippet{
    -webkit-transform: scale(1);
            transform: scale(1);
}
#Credential div.FormSnippet.Form-1>div{
    background: -webkit-linear-gradient(var(--color-blue-dark), var(--color-blue-normal));
    background: linear-gradient(var(--color-blue-dark), var(--color-blue-normal));
    border: 1px solid var(--color-blue-dark);
}
#Credential div.FormSnippet.Form-2>div{
    background: -webkit-linear-gradient(var(--color-green-dark), var(--color-green-normal));
    background: linear-gradient(var(--color-green-dark), var(--color-green-normal));
    border: 1px solid var(--color-green-dark);
}
#Credential div.FormSnippet.Form-3>div{
    background: -webkit-linear-gradient(var(--color-cyan-dark), var(--color-cyan-normal));
    background: linear-gradient(var(--color-cyan-dark), var(--color-cyan-normal));
    border: 1px solid var(--color-cyan-dark);
}
#Credential div.FormSnippet>span.Arrow::after{
    content: "";
    border-left: 1rem solid transparent;
    border-right: 1rem solid transparent; 
    position: absolute;
    bottom: calc(0.25rem + 1px);
    left: calc(50% - 1rem);
    -webkit-filter: drop-shadow(0px 1px 1px var(--color-grey-dark));
            filter: drop-shadow(0px 1px 1px var(--color-grey-dark));
}
#Credential div.FormSnippet.Form-1>span.Arrow::after{
    border-top:1rem solid var(--color-blue-normal); 
}
#Credential div.FormSnippet.Form-2>span.Arrow::after{
    border-top:1rem solid var(--color-green-normal); 
}
#Credential div.FormSnippet.Form-3>span.Arrow::after{
    border-top:1rem solid var(--color-cyan-normal); 
}
#Credential>button.Confirm{
    margin-left: auto;
    margin-right: auto;
    display: block;
    padding-left: 2.25rem;
    padding-right: 2.25rem;
}
ul.slick-dots button{
    box-shadow: none;
}

/* The followings are for formsnippts' content */
#Credential div.FormSnippet>div{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding: 0.75rem 1rem;
}
#Credential div.FormSnippet>div>.Title{
    width: 100%;
    font-weight: 500;
    white-space: nowrap;
    overflow: hidden;
    -webkit-animation: TitleScrollAnimation 6s forwards;
            animation: TitleScrollAnimation 6s forwards;
    margin-bottom: 1.25rem;
    text-align: center;
}
#Credential div.FormSnippet>div>.Title>i{
    margin-right: 1rem;
    font-size: var(--font-size-large);
}
div.MessageScreen{
    height: 100vh;
    width: 100vw;
    max-width: calc(576px + calc((100vw - 576px) * 0.2));
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    z-index: 10;
    padding: 1rem 1.5rem;
    -webkit-animation: FadeIn 1.5s;
            animation: FadeIn 1.5s;
}

div.MessageScreen.error{
    background: -webkit-linear-gradient(rgba(255, 1, 0, 0.8), rgba(140, 40, 10, 0.8));
    background: linear-gradient(rgba(255, 1, 0, 0.8), rgba(140, 40, 10, 0.8));
}
div.MessageScreen.success{
    background: -webkit-linear-gradient(rgba(0, 255, 0, 0.8), rgba(22, 78, 6, 0.8));
    background: linear-gradient(rgba(0, 255, 0, 0.8), rgba(22, 78, 6, 0.8));
}
div.MessageScreen.info{
    background: -webkit-linear-gradient(rgba(16, 243, 232, 0.9), rgba(20, 99, 154, 0.9));
    background: linear-gradient(rgba(16, 243, 232, 0.9), rgba(20, 99, 154, 0.9));
}
div.MessageScreen.search{
    background: -webkit-linear-gradient(rgba(235, 235, 235, 0.9), rgba(200, 200, 200, 0.9));
    background: linear-gradient(rgba(235, 235, 235, 0.9), rgba(200, 200, 200, 0.9));
    justify-content: space-around;
}
div.MessageScreen>div.Content{
    width: 100%;
    color: var(--color-white-light);
    font-weight: 500;
    text-transform: capitalize;
    font-size: var(--font-size-large);
    letter-spacing: 0.1rem;
    white-space: pre-wrap;
    text-align: center;
}
div.MessageScreen>button{
    padding: 0.25rem 1.5rem;
    min-width: 10rem;
    margin-top: 1.5rem;
    font-weight: 600;
}
#pre-form>button,
#success-form>button{
    padding: 0.25rem 0.5rem;
    display: block;
    width: 100%;
    max-width: 20rem;
    margin-left: auto;
    margin-right: auto;
    font-size: var(--font-size-small);
}
#form-0>p.Goback{
    text-align: left;
    padding: 0 1.25rem;
    color: var(--color-grey-normal);
}
#form-0>p.Goback>span:hover{
    cursor: pointer;
}
#signup-promo{
    color: var(--color-green-light);
    font-weight: 600;
    letter-spacing: 0.2rem;
    text-transform: uppercase;
}
#signup-promo::-webkit-input-placeholder{
    font-weight: 400;
    letter-spacing: 0;
}
#signup-promo::-ms-input-placeholder{
    font-weight: 400;
    letter-spacing: 0;
}
#signup-promo::placeholder{
    font-weight: 400;
    letter-spacing: 0;
}
#form-2>div.CheckCondition{
    max-width: 20rem;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 1rem;
}
#form-2>div.CheckCondition>span>i{
    color: var(--color-gold-light);
    font-weight: 600;
}
#form-2>div.CheckCondition>span>i:hover{
    cursor: pointer;
}
#form-2>div.CheckCondition>input[type='checkbox']{
    display: none;
}
#form-2>div.CheckCondition>label{
    display: block;
    width: 5rem;
    height: 1.5rem;
    background-color: var(--color-grey-dark);
    border-radius: 0.75rem;
    margin: 0;
    position: relative;
    -webkit-transition: background-color 1.5s;
    transition: background-color 1.5s;
    margin: 0 0.75rem 0.75rem 0;
}
#form-2>div.CheckCondition>label:hover{
    cursor: pointer;
}
#form-2>div.CheckCondition>label>span{
    padding: 0.75rem;
    font-size: 0;
    background-color: var(--color-white-dark);
    border-radius: 50%;
    position: absolute;
    top: 0;
    left: 0;
    -webkit-transition: left 2s;
    transition: left 2s;
}
#form-2>div.CheckCondition>input[type='checkbox']:checked+label{
    background-color: var(--color-green-normal);
}
#form-2>div.CheckCondition>input[type='checkbox']:checked+label>span{
    left: 3.5rem;
}
